import React from 'react';
import { storiesOf } from '@storybook/react';
import { withKnobs, text } from '@storybook/addon-knobs';

import README from '@availity/avatar/README.md';

import { Preview } from '../util';

const Avatar = React.lazy(() => import('@availity/avatar'));

storiesOf('Components/Avatar', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } }).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
  .addParameters({
    readme: {
      // Show readme at the addons panel
      sidebar: README,
      StoryPreview: Preview,
    },
  })
  .addDecorator(withKnobs)
  .add('default', () => (
    <div className="py-3">
      <Avatar
        fallback={text('Fallback', '/public/apps/my-profile/images/Avatars-00.png')}
        skeletonProps={{
          width: text('Skeleton Width', '350px'),
          height: text('Skeleton Height', '350px'),
        }}
      />
    </div>
  ));
