import React from 'react';
import { storiesOf } from '@storybook/react';
import { withKnobs, boolean, number, text } from '@storybook/addon-knobs';

import README from '@availity/progress/README.md';

import { Preview } from '../util';

const Progress = React.lazy(() => import('@availity/progress'));

storiesOf('Components/Progress', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } }).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
  .addParameters({
    readme: {
      // Show readme at the addons panel
      sidebar: README,
      StoryPreview: Preview,
    },
  })
  .addDecorator(withKnobs)
  .add('default', () => {
    const value = number('Value', 50);
    const max = number('Max', 100);
    return (
      <div>
        <p>{Math.round((value / max) * 100)}% Complete</p>
        <Progress
          animated={boolean('Animated', false)}
          striped={boolean('Striped', false)}
          complete={boolean('Complete', false)}
          color={text('Color', 'success')}
          value={value}
          max={max}
        />
      </div>
    );
  });
